import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import FormLabel from '@material-ui/core/FormLabel';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import RadioGroup from '@material-ui/core/RadioGroup';
import Radio from '@material-ui/core/Radio';
import Paper from '@material-ui/core/Paper';
import './default.css'
import ProfilePic from "../images/boba.png";
import CurrencyApp from "../images/cc1.PNG";
import FT from "../images/FT.PNG";
import Food from "../images/Food.PNG";
import Book from "../images/Book.PNG";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    display: 'flex',
    flexDirection: 'row',
    marginTop: 20,
    height: 300,
    width: 660,
    backgroundColor: '#e2eca9'
  },
  thumbnail:{
    width: 260,
    height: 300,
  },
  control: {
    padding: theme.spacing(2),
  },
}));

export default function ProjectSection() {
  const classes = useStyles();

  const handleClick = (link) =>{
    console.log("clicked" + link);
    window.open(link, "_blank");
  }
  return (
      <Grid item xs={12}>
        <Grid id="projectContainer" container justify="center" spacing={12}>
          {/* {[0, 1, 2, 3].map((value) => (
            <Grid key={value} item>
              <Paper className={classes.paper}>
              </Paper>
            </Grid>
          ))} */}
          <Paper className={classes.paper} onClick={() => handleClick("https://github.com/MaxBulatov/BooksApp")}>
            <img className={classes.thumbnail} src={Book} alt=""/>
            <div class="post-preivew">
                <h3 class="post-title">Book Database Website</h3>
                <p class="post-text">
                Using Google Books API you can search for a book franchise you enjoy and get 
                information about the book name, book genre and book description in this website.
                </p>
                <h3>Click anywhere to check it out!</h3>
            </div>
          </Paper>
          <Paper className={classes.paper} onClick={() => handleClick("https://food-guide-api.herokuapp.com/")}>
              <img className={classes.thumbnail} src={Food} alt=""/>
              <div class="post-preivew">
                  <h3 class="post-title">Food Recipe Guide</h3>
                  <p class="post-text">
                  Using Edamamne Food API you can search for food recipe's with a specific theme, like
                  chicken, beef or noodles for example.
                  </p>
                  <h3>Click anywhere to check it out!</h3>
              </div>
          </Paper>
          <Paper className={classes.paper} onClick={() => handleClick("https://github.com/MaxBulatov/CurrencyExchange")}>
            <img className={classes.thumbnail} src={CurrencyApp} alt=""/>
              <div class="post-preivew">
                  <h3 class="post-title">CurrencyExchange</h3>
                  <p class="post-text">
                    Using Exchange Rate API you can see the currency ratio between counties.
                  </p>
                  <h3>Click anywhere to check it out!</h3>
              </div>
          </Paper>
          <Paper className={classes.paper} onClick={() => handleClick("https://floral-trauma-mmst.herokuapp.com/")}>
            <img className={classes.thumbnail} src={FT} alt=""/>
              <div class="post-preivew">
                  <h3 class="post-title">Floral Trauma Multiplayer Game</h3>
                  <p class="post-text" style={{textAlign: 'left', fontSize: 14}}>
                    Our graduating class of 24 students worked on this game thought out our 4th semester at BCIT. 
                    My personal contribution to this project was testing and quality assurance. I was the leader of a small team, 
                    insuring that new features being developed worked properly and all code followed a coding standard based on common industry standards.
                    This was acheived through conducting usability studies on game design descisions as well as conducting function and user testing
                    to make sure the game ran smoothly.
                  </p>
                  <h3>Click anywhere to check it out!</h3>
              </div>
          </Paper>

        </Grid>
      </Grid>
  );
}
